<template>
    <div>
        <Table :columns="table.cols" :data="table.data"></Table>
        <Button type="primary" @click.prevent="$router.go(-1)" style="margin-left: 8px">返回</Button>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
export default {
    name: 'DistDownSubPro',
    mixins: [TableColRender, TableDataView],
    data () {
        return {
            table: {
                url: '/api/channel/group/sub/list',
                query: {
                    id: this.$route.params.channelId
                },
                cols: [
                    {
                        title: '子产品名',
                        key: 'name'
                    },
                    {
                        title: '供应商',
                        key: 'epName'
                    },
                    {
                        title: '市场价',
                        key: 'marketPrice',
                        render: this.renderMoney
                    },
                    {
                        title: '创建时间',
                        key: 'createTime',
                        render: this.renderDate
                    },
                    {
                        title: '操作',
                        render: (h, params) =>
                            this.renderActions(h, params, [
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.DownSubPro(params.row),
                                    text: '下架'
                                }
                            ])
                    }
                ],
                data: []
            }
        };
    },
    methods: {
        async DownSubPro (row) {
            const result = await this.post('api/channel/group/sub/down', {
                channelId: this.$route.params.channelId,
                productsubId: row.id
            });
            if (result.success) {
                this.$Message.success('下架成功');
                this.doQuery();
            }
        }
    }
};
</script>
